@import "mixin";

@media all and (max-width: 720px) {

	body {
		min-width: $w375;
		margin: auto; }
	.bunner {
		background: $white;
		max-width: $w375;
		margin: auto;
		position: relative;
		.ninja {
				background: url("../image/Menu-1.png") center center no-repeat; }
		nav {
			height: 1238px;
			a {
				color: $white; }
			.logo {
				div {
					color: $white; } } }
		.st-effect-1.st-menu-open .st-effect-1.st-menu {
			visibility: visible;
			transform: translate3d(340%, 0, 0);
			z-index: 300; } }

	.site-h {
		margin-top: 40px; }
	.writing {
		width: $w375;
		display: block;
		.product {
			width: 100%; }
		.product:nth-child(n+3) {
			display: none; } } }

